.about{
    background-color: rgba(80, 80, 80, 0.5);
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items:center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{
    text-align:center;
    margin: auto;
    padding: 1rem;
    max-width: 400px;
}
.about .left p{
    margin: 1.2rem 0;
}

.about .right{
    max-width: 700px;
}
.right .img-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}
.right .img {
    
    border: 1px solid white;
    border-radius: 50%;
  }

  @media screen and (max-width: 640px){
    .about{
        grid-template-columns: 1fr;
    }
}